    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html, body {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: var(--vscode-editor-background, #1e1e1e);
    color: var(--vscode-editor-foreground, #cccccc);
    font-family: var(--vscode-font-family, -apple-system, BlinkMacSystemFont, 'Segoe UI', sans-serif);
    font-size: var(--vscode-font-size, 13px);
}

#viewer-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 90px; /* command-panel(65px) + status-bar(24px) + 1px */
    overflow: hidden;
}

#viewer-container canvas {
    display: block;
    width: 100%;
    height: 100%;
}

#toolbar {
    position: absolute;
    top: 10px;
    left: 10px;
    display: none; /* Hidden by default - use right-click context menu instead */
    flex-direction: column;
    gap: 2px;
    padding: 4px;
    background-color: #3a3a3a;
    border: 1px solid #5a5a5a;
    border-radius: 3px;
    z-index: 100;
}

#toolbar.visible {
    display: flex;
}

#toolbar button {
    padding: 4px 8px;
    background-color: #4a4a4a;
    color: #cccccc;
    border: none;
    border-radius: 2px;
    cursor: pointer;
    font-size: 11px;
    min-width: 50px;
    text-align: center;
}

#toolbar button:hover {
    background-color: #5a5a5a;
}

#toolbar button:active {
    background-color: #0066cc;
}

#toolbar button.active {
    background-color: #0066cc;
    color: #ffffff;
}

#toolbar .separator {
    height: 1px;
    background-color: #5a5a5a;
    margin: 2px 0;
}

#toolbar .button-row {
    display: flex;
    gap: 2px;
}

#toolbar .toolbar-group {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

#toolbar .toolbar-label {
    font-size: 9px;
    color: #888888;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    padding: 0 2px;
    margin-bottom: 1px;
}

#status-bar {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    height: 24px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
    background-color: var(--vscode-statusBar-background, #007acc);
    color: var(--vscode-statusBar-foreground, #ffffff);
    font-size: 12px;
}

#status-text {
    flex: 1;
}

#coords {
    font-family: monospace;
    margin-left: 10px;
}

#loading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    color: var(--vscode-editor-foreground, #cccccc);
    z-index: 200;
    background-color: rgba(30, 30, 30, 0.9);
    padding: 20px 40px;
    border-radius: 8px;
}

.spinner {
    width: 40px;
    height: 40px;
    border: 3px solid var(--vscode-panel-border, #3c3c3c);
    border-top-color: var(--vscode-button-background, #0e639c);
    border-radius: 50%;
    animation: spin 1s linear infinite;
}

@keyframes spin {
    to {
        transform: rotate(360deg);
    }
}

/* Layer Toolbar - AutoCAD style top bar */
#layer-toolbar {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 28px;
    background-color: #3a3a3a;
    border-bottom: 1px solid #5a5a5a;
    display: none;
    align-items: center;
    padding: 0 8px;
    gap: 8px;
    z-index: 95;
    font-size: 11px;
}

#layer-toolbar.visible {
    display: flex;
}

#layer-toolbar-label {
    color: #888;
    font-size: 10px;
    white-space: nowrap;
}

#layer-toolbar-select {
    min-width: 180px;
    max-width: 250px;
    padding: 3px 8px;
    background-color: #2d2d2d;
    color: #cccccc;
    border: 1px solid #5a5a5a;
    border-radius: 3px;
    font-size: 11px;
    cursor: pointer;
}

#layer-toolbar-select:hover {
    border-color: #007acc;
}

#layer-toolbar-select:focus {
    outline: none;
    border-color: #007acc;
}

.layer-toolbar-btn {
    width: 22px;
    height: 22px;
    padding: 0;
    background-color: transparent;
    border: 1px solid transparent;
    border-radius: 3px;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #cccccc;
    font-size: 12px;
}

.layer-toolbar-btn:hover {
    background-color: #4a4a4a;
    border-color: #5a5a5a;
}

.layer-toolbar-btn.active {
    background-color: #264f78;
