}

.layer-toolbar-btn-delete {
    font-size: 16px;
    font-weight: bold;
    color: #f14c4c;
}

.layer-toolbar-btn-delete:hover {
    background-color: #5a2d2d;
    border-color: #f14c4c;
}

.layer-toolbar-btn-delete:disabled {
    color: #666666;
    cursor: not-allowed;
}

.layer-toolbar-btn-delete:disabled:hover {
    background-color: transparent;
    border-color: transparent;
}

.layer-toolbar-separator {
    width: 1px;
    height: 18px;
    background-color: #5a5a5a;
    margin: 0 4px;
}

.layer-toolbar-color {
    width: 14px;
    height: 14px;
    border: 1px solid #888;
    border-radius: 2px;
    margin-right: 6px;
}

.layer-toolbar-props {
    display: flex;
    align-items: center;
    gap: 6px;
}

.layer-toolbar-prop-label {
    color: #888;
    font-size: 10px;
}

.layer-toolbar-color-box {
    width: 18px;
    height: 14px;
    border: 1px solid #666;
    border-radius: 2px;
    background-color: #ffffff;
    cursor: pointer;
}

.layer-toolbar-color-box:hover {
    border-color: #007acc;
}

.layer-toolbar-linetype {
    min-width: 60px;
    padding: 2px 4px;
    background-color: #2d2d2d;
    border: 1px solid #5a5a5a;
    border-radius: 2px;
    color: #ccc;
    font-size: 10px;
    text-align: center;
    cursor: pointer;
    text-transform: capitalize;
}

.layer-toolbar-linetype:hover {
    border-color: #007acc;
}

.layer-toolbar-lineweight {
    min-width: 35px;
    padding: 2px 4px;
    background-color: #2d2d2d;
    border: 1px solid #5a5a5a;
    border-radius: 2px;
    color: #ccc;
    font-size: 10px;
    text-align: center;
    cursor: pointer;
}

.layer-toolbar-lineweight:hover {
    border-color: #007acc;
}

.layer-toolbar-info {
    display: flex;
    align-items: center;
    gap: 4px;
    color: #aaa;
    font-size: 10px;
    margin-left: auto;
}

.layer-toolbar-close {
    margin-left: 8px;
    background: transparent;
    border: none;
    color: #888;
    font-size: 14px;
    cursor: pointer;
    padding: 2px 6px;
}

.layer-toolbar-close:hover {
    color: #fff;
    background-color: #c42b1c;
    border-radius: 3px;
}

/* Layer toolbar snap/ortho section */
.layer-toolbar-snap-section {
    display: flex;
    align-items: center;
    gap: 4px;
}

.layer-toolbar-snap-btn,
.layer-toolbar-ortho-btn {
    padding: 2px 6px;
    background-color: #2d2d2d;
    border: 1px solid #5a5a5a;
    border-radius: 2px;
    color: #888;
    font-size: 9px;
    font-weight: bold;
    cursor: pointer;
    user-select: none;
    transition: all 0.15s;
}

.layer-toolbar-snap-btn:hover,
.layer-toolbar-ortho-btn:hover {
    border-color: #007acc;
